export default {
  // rgba 
  colorToRGB(val, opa) {
    var pattern = /^(#?)[a-fA-F0-9]{6}$/; //16
      var isOpa = typeof opa == 'number'; //

      if (!pattern.test(val)) { //
          return '';
      }

      var v = val.replace(/#/, ''); //##
      var rgbArr = [];
      var rgbStr = '';

      for (var i = 0; i < 3; i++) {
          var item = v.substring(i * 2, i * 2 + 2);
          var num = parseInt(item, 16);
          rgbArr.push(num);
      }

      rgbStr = rgbArr.join();
      rgbStr = 'rgb' + (isOpa ? 'a' : '') + '(' + rgbStr + (isOpa ? ',' + opa : '') + ')';
      return rgbStr;
  },

  // vuex
  setBreadcrumb({prevTitle, prevUrl, currentTitle} = {}) {
    store.commit('setbBreadcrumbInfo', {
      prevTitle: prevTitle || '',
      prevUrl: prevUrl || '/',
      currentTitle: currentTitle || $vue.$route.meta.title
    })
  },

  // 
  emptyObj(val, isEnumerable = false) {
    return !!(Object.prototype.toString.call(val) === '[object Object]' ? 
    isEnumerable ? 
    Reflect.ownKeys(val) : 
    Object.keys(val).length : 
    false)
  },

  // 
  statusList: [
    // 
    {code: 'LINE', name: ''},
    {code: 'OFFLINE', name: ''},
  ],

  // 
  articlesType: [{
    name: ''
  }]
}