import axios from '../axios'

export default {
  // 
  addLabel(data) {
    return axios.post('/label/addLabel', data)
  },
  // 
  queryLabelList(data) {
    return axios.post('/label/list', data)
  },
  // 
  updateLabel(data) {
    return axios.post('/label/updateLabel', data)
  },
  // 
  deleteLabel(data) {
    return axios.post('/label/deleteLabel', data)
  },
  // 
  updateHotLabel(data) {
    return axios.post('/label/hot', data)
  }
}